const header = document.querySelector(".header");

if (header) {
    const links = header.querySelectorAll(".header__nav-link_sub, .header__nav-link_menu");
    const submenus = header.querySelectorAll(".header__submenu");

    let openedSubmemu = undefined;

    const closeAllSubmenus = () => {
        submenus.forEach((menu) => menu.classList.remove("opened"));
        links.forEach((anotherLink) => anotherLink.classList.remove("opened"));
    };

    const onWindowClick = (event) => {
        if (!event.target.matches(".header__submenu, .header__submenu *")) {
            closeAllSubmenus();
            document.removeEventListener("click", onWindowClick);
        }
    };

    links.forEach((link, index) =>
        link.addEventListener("click", (event) => {
            event.preventDefault();
            event.stopPropagation();
            const submenu = link.closest(".header__nav-item").querySelector(".header__submenu");

            if (openedSubmemu === index) {
                link.classList.remove("opened");
                submenu.classList.remove("opened");
                openedSubmemu = undefined;
                document.removeEventListener("click", onWindowClick);
            } else {
                closeAllSubmenus();
                link.classList.add("opened");
                submenu.classList.add("opened");
                openedSubmemu = index;
                document.addEventListener("click", onWindowClick);
            }
        })
    );
}
